// @ts-nocheck
// @ts-ignore
import router from "../router/index";
import api from "../../api/api";
const state = {
  clubData: null,
  allClubs: [],
};

const getters = {};
const actions = {
  async getClubData({ commit }) {
    api
      .get("/clubs/getmyclub")
      .then((res) => {
        commit("SET_CLUB_DATA", res.data.data);
      })
      .catch((error) => {
        console.log(error);
      });
  },
  async registerClubs({ commit }, clubdata) {
    console.log(clubdata);
    api
      .post("/clubs/registerclub", clubdata)
      .then((res) => {
        console.log(res.data.data);
        commit("SET_CLUB_DATA", res.data.data);
        router.push("/clubadmin");
      })
      .catch((error) => {
        console.log(error);
      });
  },
  async getAllClubs({ commit }) {
    api
      .get("/clubs/")
      .then((res) => {
        commit("SET_ALL_CLUBS", res.data);
      })
      .catch((error) => {
        console.log(error);
      });
  },
};

const mutations = {
  ["SET_CLUB_DATA"](state, data) {
    state.clubData = data;
  },
  ["SET_ALL_CLUBS"](state, data) {
    console.log(data);
    state.allClubs = data;
  },
};
export default {
  state,
  getters,
  actions,
  mutations,
};
